import React, { useEffect, useState } from "react";
import "bootstrap/dist/css/bootstrap.css";
import { fetchMatches } from "./api/api";
import Navbar from "./components/Navbar";
import MyCard from "./components/Mycard";
import FooterBar from "./components/FooterBar";
import "./swicther.scss";
import "./App.css";

function App() {
  const [matches, setMatches] = useState([]);
  const [visbile, setVisbile] = useState(3);

  const loadMoreMatches = () => {
    // setVisbile((prevState) => {
    //   prevState = prevState + 3;
    // });

    setVisbile((prev) => prev + 3);
  };

  useEffect(() => {
    fetchMatches()
      .then((data) => {
        console.log(data);
        setMatches(data.matches);
      })
      .catch((error) => alert(error));
  }, []);

  const [isDark, setIsDark] = useState(false);
  useEffect(() => {
    //local stoarge check
    console.log(isDark, "test");
    const currentThemeColor = localStorage.getItem("theme-color");
    if (currentThemeColor === "theme-dark") {
      setIsDark(true);
    } else {
      setIsDark(false);
    }
  }, []);
  const setValue = (val) => {
    if (val) {
      localStorage.setItem("theme-color", "theme-light");
      setIsDark(false);
    } else {
      localStorage.setItem("theme-color", "theme-dark");
      setIsDark(true);
    }
  };

  return (
    <>
      <div className={`App ${isDark ? "theme-dark" : "theme-light"}`}>
        <div className="checkbox checbox-switch switch-primary">
          <label>
            <input
              type="checkbox"
              value={isDark}
              onChange={() => setValue(isDark)}
            />
            <span></span>
          </label>
        </div>
        <Navbar />
        <div className="container-fluid">
          <div className="row">
            {matches.slice(0, visbile).map((item, i) => (
              <MyCard key={i} {...item} />
            ))}
          </div>
        </div>
        <div className="text-center">
          <button
            type="button"
            className="show-button"
            onClick={loadMoreMatches}
          >
            Load More Matches
          </button>
        </div>
        <FooterBar />
      </div>
    </>
  );
}

export default App;
